import WeatherService from '@/services/WeatherDataService.js'


export const namespaced = true 

export const state = {
    reqCity: '',
    user_location_forecast: undefined,
    user_location_current_weather: undefined,
    slovakia: {
        cities: [],
        country: []
    },
    countyCities: [],
    europeanCities: [],

}

export const mutations = {

    /** meno mesta, potrebne pre vyhladavanie inputom, pre update v dashboarde */
    SET_CITY( state, data ) {
        state.reqCity = data
    },

    /** mesta pre slovakia view */
    SET_SLOVAKIA_CITIES( state, data ) {
        state.slovakia.cities = data
    },

    /** Slovensko overall pre slovakia view */
    SET_SLOVAKIA_COUNTRY( state, data ) {
        state.slovakia.country = data
    },

    /** user pocasie aby sa nevysielalo viac api calls po prvom nacitani */
    SET_USER_LOCATION_WEATHER( state, data ) {
        state.user_location_current_weather = data
    },

    /** user predpoved */
    SET_USER_LOCATION_FORECAST( state, data ) {
        state.user_location_forecast = data
    },

    /** mesta kraja */
    SET_COUNTY_CITIES( state, data ) {
        state.countyCities = data
    },

    /** europa overall */
    SET_EUROPEAN_CITIES( state, data ) {
        state.europeanCities = data
    }
}

export const actions = {

    /** vyhlada pocasie a predpoved */
    fetchCityWeather( context, name ) {
        return WeatherService.getByCityName(name).then(res => {
            const current = res.data

            return WeatherService.getOneCall(res.data.coord.lon, res.data.coord.lat).then(res => {
                const data = {
                    current,
                    forecast: res.data                   
                }
                return data
            })
        })
    },
    
    /** user predpoved a volanie pocasia */
    getInitialCityForecast({ state, commit, dispatch }, { lon, lat } ) {
        if(state.user_location_forecast == undefined ) {

            return WeatherService.getOneCall(lon, lat).then(res => {
                commit('SET_USER_LOCATION_FORECAST', res.data )
                const forecast = res.data

                return dispatch('getInitialCityWeather', { lon, lat }).then(res => {
                    const data = {
                        current: res,
                        forecast
                    }
                    return data
                })
            })
        }
    },

    /** user location pocasie */
    getInitialCityWeather({ commit }, { lon, lat }) {
        return WeatherService.getByCoords(lon, lat).then(res => {
            commit('SET_USER_LOCATION_WEATHER', res.data)
            return res.data
        })
    },

    /** mapa slovenska */
    getSlovakiaCities({ state, commit }, ids ) {
        if( !state.slovakia.cities.list ) {
            return WeatherService.getMultipleCities(ids).then(res => {
                commit('SET_SLOVAKIA_CITIES', res.data.list )
                return res.data
            })
        }
    },

     /** slovenko overall pre slovakia view */
     getSlovakia({ commit }) {
        if( state.slovakia.country.length == 0 ) {
            return WeatherService.fetchSlovakia().then(res => {
                commit('SET_SLOVAKIA_COUNTRY', res.data)
                return res.data
            })
        }
    },

    /** mesta kraja */
    getCountyCities({ commit }, ids ) {
        return WeatherService.getMultipleCities(ids).then(res => {
            commit('SET_COUNTY_CITIES', res.data.list)
            return res.data.list
        })
    },

    /** europa overall */
    getEuropeanCities({ commit }, ids ) {
        return WeatherService.getMultipleCities(ids).then(res => {
            commit('SET_EUROPEAN_CITIES', res.data.list)
            return res.data.list
        })
    },
}