import Vue from 'vue'
import Router from 'vue-router'
import NProgress from 'nprogress'
import store from '@/store/store'

import Dashboard from '@/views/Dashboard'
import Slovakia from '@/views/Slovakia'
import Europe from '@/views/Europe'
import InteractiveMap from '@/views/InteractiveMap'
import NotFound from '@/views/error/404'

Vue.use(Router)

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/slovakia',
            name: 'slovakia',
            component: Slovakia,
            props: true,
            beforeEnter(to, from, next) {
                if(store.state.weather.slovakia.cities == 0) {  
                    const ids = [3057568, 724627, 3058986,3343957,3060972,865085,3060835,3056506,3057691,3058531,723846,3343954,865084]
                    store.dispatch('weather/getSlovakiaCities', ids).then(res => {
                        to.params.cities = res.list
                        store.dispatch('weather/getSlovakia').then(res => {
                            to.params.svk = res
                            next()
                        })
                    })
                } else {
                    to.params.cities = store.state.weather.slovakia.cities
                    to.params.svk = store.state.weather.slovakia.country
                    next()
                }
            }
        },
        {
            path: '/europe',
            name: 'europe',
            component: Europe,
            props: true,
            beforeEnter(to, from, next) {
                if( store.state.weather.europeanCities.length == 0) {
                    const ids = [2988507, 2950159, 756135, 3117735, 3067696, 3054643, 2618425, 2643743, 3169070, 2761369, 683506, 264371, 2464470, 703448, 2964574, 2657896]
                    store.dispatch('weather/getEuropeanCities', ids).then((res) => {
                        to.params.cities = res
                        next()
                    })
                } else {
                    to.params.cities = store.state.weather.europeanCities 
                    next()
                }
        }
        },
       
        {
            path: '/slovakia/interactive',
            name: 'interactive',
            component: InteractiveMap,
        },

        {
            path: '/myLocation',
            component: Dashboard,
            name: 'my-location',
            props: true,
            beforeEnter(to, from, next) {
                function getLocation() {
                    if( navigator.geolocation ) {
                        navigator.geolocation.getCurrentPosition(getLocationData)
                    } else { 
                        next({ name: 'slovakia'})
                    }
                }

                function getLocationData(position) {
                    let longitude = position.coords.longitude
                    let latitude = position.coords.latitude

                    store.dispatch('weather/getInitialCityForecast', { lon: longitude, lat: latitude }).then((res) => {
                        to.params.current = res.current
                        to.params.forecast = res.forecast
                        next()
                    }).catch(() => {
                        next({ name: 'not-found', params: { resource: 'city'}})
                    })
                }

                /**
                 * ak nie je poloha usera nacitana, zisti ju, zavolaj API,
                 * ak je tak len stiahni info a predaj to params v router z vuex
                 */
                if( store.state.weather.user_location_forecast == undefined 
                    && store.state.weather.user_location_forecast == undefined ) {
                    getLocation()
                } else {
                    to.params.current = store.state.weather.user_location_current_weather
                    to.params.forecast = store.state.weather.user_location_forecast
                    next()
                }
            },
        },

        {
            path: '/weather/:city',
            name: 'home',
            component: Dashboard,
            props: true,
            beforeEnter(to, from, next) {
                if( store.state.weather.reqCity == '') 
                    store.commit('weather/SET_CITY', to.params.city)

                    store.dispatch('weather/fetchCityWeather', to.params.city)
                    .then(res => {
                        to.params.current = res.current
                        to.params.forecast = res.forecast
                        next()
                    }).catch(() => {
                        next({ name: 'not-found', params: { resource: 'city'}})
                    })
                },
            },
        {
            path: '/',
            redirect: { name: 'my-location'}
        },
        {
            path: '/404',
            name: 'not-found',
            component: NotFound,
            props: true
        },

        {
            path: '*',
            redirect: { name: 'not-found', params: { resource: 'page'}}
        },
       
        {
        path: '*',
        name: 'catchAll',
        component: Dashboard
     }
    ]
});

router.beforeEach((to, from, next) => {
    NProgress.start()
    next({
        query: { redirect: to.fullPath}
    })
})

router.afterEach(() => {
    NProgress.done()
}) 

export default router;