<template>
  <div ref="container">
      <div class="flex justify-around mb-4 py-8 px-12 display">
        <div class="flex items-center">
        <h3 class="city">{{ current.name }}</h3>
        </div>
        <div class="overview"> 
          <div class="flex justify-center">
            <SvgIcons :description="current.weather[0].description" :width="icon.width" :height="icon.height" />
              <div class="main-display">
                <p class="font-light temp">{{ current.main.temp | temp }}</p>
                <p class="font-light weather-desc">{{ current.weather[0].description | capitalize }}</p>
              </div>
          </div>
        </div>
      </div>
    <div class="mt-4 mb-8 p-8 text-left details">
      <h4 class="font-bold pb-2">Details</h4>
      <div class="flex justify-between relative">
        <table class=" self-center weather-table">
          <tr class="odd">
            <td>Feels like:</td>
            <td>{{ current.main.feels_like | temp }}</td>
          </tr>
          <tr class="even">
            <td>Humidity:</td>
            <td>{{ current.main.humidity }} %</td>
          </tr>
          <tr class="odd">
            <td>Pressure:</td>
            <td>{{ current.main.pressure }} hPa</td>
          </tr>
          <tr class="even">
            <td>Wind speed:</td>
            <td>{{  current.wind.speed | KmH }}</td>
          </tr>
        </table>
        <WeatherChart v-if="forecast" :forecast="forecast.hourly" />
      </div>
    </div>
      <h1 class="text-4xl">Forecast for 8 days</h1>
      <div class="flex flex-col">

        <div class="flex mt-6 justify-between forecast">
          <div v-if="forecast!== undefined">
              <ForecastCard 
                  v-for="(day, i) in forecast.daily" 
                  :key="i"
                  :day="day"
                  :id="i"
                  @activateDay="activeDay = $event"
                  :isActive="activeDay.id == i ? true : false"
              />
          </div>
            <ForecastReport v-if="activeDay.day" :day="activeDay.day" />
        </div>
      </div>
    </div>
</template>

<script>
import ForecastCard from '@/components/ForecastCard'
import SvgIcons from '@/components/svgs/SvgIcons'
import ForecastReport from '@/components/ForecastReport'
import WeatherChart from '@/components/charts/WeatherChart'
import MediaQueriesMixin from '@/mixins/MediaQueriesMixin.js'

export default {
  mixins: [ MediaQueriesMixin ],
  components: { SvgIcons, ForecastCard, ForecastReport, WeatherChart },

  props: {
    forecast:{
      type: Object,
      required: true
    },
    current: {
      type: Object,
      required: true
    }
  },

  filters: {
    capitalize(val) {
      return val.charAt(0).toUpperCase() + val.slice(1)
    }
  },

  mounted() {
    this.activeDay.day = this.forecast.daily[0]
  },


  beforeRouteUpdate(to, from, next) {
    const city = this.$store.state.weather.reqCity
    this.$store.dispatch('weather/fetchCityWeather', city).then(res => {
      to.params.current = res.current
      to.params.forecast = res.forecast
      next()
    }).catch(() => {
      next({ name: 'not-found', params: { resource: 'city'}})
    })
  },

  data() {
    return {
      graph: {
        width: 500
      },
      icon: {
        width: null,
        height: null
      },
      activeDay: {
        day: {},
        id: 0
      },
    }
  },

  watch: {
    width(size) {
           if(size <= 1050 && size > 830) {
              this.graph.width = 400
           } else if (size <= 830 && size > 680) {
              this.icon.width = 100
              this.icon.height = 100
              this.graph.width = 340
           } else if (size < 680 && size > 650) {
             this.icon.width = 100
              this.icon.height = 100
              this.graph.width = 400
           } else if (size < 650 ) {
              this.icon.width = 50
              this.icon.height = 50
              this.graph.width = 400
           } else if (size > 1050) {
              this.icon.width = 140
              this.icon.height = 140
              this.graph.width = 500
    
           }
      }
  },

}
</script>

<style scoped>

.display {
   background-color:rgba(0,0,0,.2)
}

.details {
  background-color: #c1c1c1;
  color: rgb(0,0,0);
}

.odd {
  background-color:rgb(90, 90, 90)
}

.even {
  background-color:rgb(180, 180, 180)
}
.weather-table {
  width: 40%;
  background-color: rgba(0,0,0,.2);
  color: rgb(0,0,0);
  border-collapse: collapse;
  margin-right: 2rem
}
.weather-table td {
  max-width: 100%;
  text-align: left;
  padding: .7rem;
  font-weight: bold
}
.selected {
    border: 1px solid rgb(2, 2, 146);
}

.overview {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.city {
    text-align: left;
    font-size: 5rem;
    word-break: break-word;
}

.main-display {
    display: flex; 
    flex-direction: column;
    justify-content: center;
    margin-left: 1.5rem; 
    text-align: left
}
.weather-desc {
  color: #cbe5ff
}

.temp {
    position: relative;
    left: -.375rem;
    font-size: 4rem;
    line-height: 1;
    color: #cbe5ff
}

@media only screen and (max-width: 1050px) {
  .city {
    font-size: 4rem;
  }

  .temp {
    font-size: 4rem;
  }
}

@media only screen and (max-width: 830px) {

    .city {
    font-size: 3rem;
  }

  .temp {
    font-size: 3rem;
  }


  .display {
    padding: 1rem
  }
  .details > h4 {
    display: none
  }

  .details > div {
    align-items: center;
  }

  .weather-table td {
    text-align: center;
    font-size: .8rem;
    padding: .4rem
  }

  .weather-table {
    max-width: 300px;
    margin-right: 0;
    margin-left: 0
  }
}

@media screen and (max-width: 680px) {
    .details > div {
      flex-direction: column-reverse
    }

    .weather-table {
      width: 100%;
      margin-top: 1rem;
    }

    .forecast {
      flex-direction: column-reverse
    }

    .forecast > div {
      margin-top: 1rem;
    }

    .main-display {
      margin-left: 1rem;
    }
}

@media screen and (max-width: 650px) {
  .display {
    flex-direction: column;
    align-items: center;
    text-align: center
  }

  .display h3 {
    text-align: center;
  }

  .temp {
    font-size: 1.5rem;
    margin-top: .5rem;
  }
}

@media screen and (max-width: 410px) {
  .weather-table td {
    padding: .4rem 0
  }
}
</style>
