<template>
    <path
        class="county"
        :title="title"
        :id="id"
        :d="d"
        :class="{ active: isActive, hovered: hovered }"
        @mouseover="mouseover"
        @mouseleave="mouseleave"
        @click="showCounty(title, id)"
  />
</template>

<script>
export default {
    props: {
        title: {
            type: String,
            required: true
        },
        id: {
            type: String,
            required: true
        },
        d: {
            type: String,
            required: true
        },
        isActive: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            hovered: false,
            active: false
        }

    },
    methods: {

        mouseover() {
            if( !this.hovered && !this.isActive ) this.hovered = true
        },

        mouseleave() {
            this.hovered = false
        },

        showCounty(title, id) {
            if(!this.isActive) {
                this.$emit('active', { title, id })
                if(this.hovered) this.hovered = false
            }
        }
    }
}
</script>

<style scoped>
    .active {
        fill: rgb(36, 255, 10)
    }

    .hovered {
        fill: #68d268;
        cursor: pointer
    }

    .county {
        transition: all .15s ease-in
    }
</style>