<template>
  <div>
    <router-link :to="{ name: 'slovakia' }" class="overview-link">Go to overview map</router-link>
    
    <div class="flex justify-center mt-4 panel">
        <ul class="py1r mr-12 self-center panel-nav">
            <li v-for="c in counties" :key="c.id"
            class="map-item" 
            @click="activateCounty(c.title, c.id)"
            :class="{ selected : activecounty == c.title }"
            >{{ c.title }}</li>
        </ul> 
            <svg
            mapsvg:geoViewBox="16.834315 49.614067 22.568332 47.731105"
            width="612.6908"
            height="304.02798"
                fill="#155415"
                stroke="#037303"
            >
            <SlovakiaCounty v-for="c in counties" :key="c.id"
                :id="c.id"
                :title="c.title"
                :d="c.d"
                @active="activecounty = $event.title; activeID = $event.id"
                :isActive=" c.title == activecounty ? true: false"
            />
            </svg>
    </div>

    <CountyCities :activeID="activeID" />
  </div>
</template>

<script>
import SlovakiaCounty from '@/components/SlovakiaCounty'
import CountyCities from '@/components/CountyCities'
import { paths } from '@/assets/js/counties.js'

export default {
    components: { SlovakiaCounty, CountyCities },

    methods: {
        activateCounty(title, id) {
            this.activecounty = title
            this.activeID = id
        }
    },

   /** activeCounty - mapa
     * activeID - id pre zobrazenie zoznamu miest
    */

    data() {
        return {
            counties: paths,
            activecounty: '',
            activeID: '',
        }
    },

    mounted() {
        this.activecounty = 'Bratislavsk'
        this.activeID = 'SK-BL'
    }

}
</script>

<style style="scss" scoped>
    .map-item {
        padding: .2rem .5rem; 
        cursor: pointer
    }
    .county-list {
        background-color: white;
        display: flex
    }

    .county-list > li {
        width: 20%
    }
    .overview-link{
        color: rgb(192, 192, 192)
    }
    .overview-link:hover {
        color: rgb(255, 255, 255);
        text-decoration: none;
    }

    .county-card {
        display: flex;
        flex-direction: column
    }
    .panel {
        position: relative
    }

    @media screen and (max-width: 880px) {
        .panel {
            flex-direction: column;
            align-items: center
        }

        .panel-nav {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            margin-bottom: 2rem;
        }
    }


    @media screen and (max-width: 680px) {
        svg {
            transform: scale(.8)
        }      

        .panel-nav {
            margin-bottom: 0;
        }  
    }

    
    @media screen and (max-width: 550px) {
        svg {
            transform: scale(.7)
        } 
        .panel-nav {
            display: none
        }    
         .panel {
            top: -2.25rem
        }   
    }

    @media screen and (max-width: 420px) {
        svg {
            transform: scale(0.5);
        }

        .panel {
            top: -6.25rem
        }
    }

    @media screen and (max-width: 360px) {
    svg {
        transform: scale(.4)
    }
    }
</style>