<template>
  <div class="flex-1">
    <ul class="weather-info" v-if="activeDay.length !== 0">
        <li>Day <span>{{ activeDay.temp.day | temp }}</span></li>
        <li>Night <span>{{ activeDay.temp.night | temp }}</span></li>
        <li>Max <span>{{ activeDay.temp.max | temp }}</span></li>
        <li>Min <span>{{ activeDay.temp.min | temp }}</span></li>
        <li>Wind speed <span>{{ activeDay.wind_speed | KmH }}</span></li>
        <li>Wind degree <span>{{ activeDay.wind_deg }}</span></li>
        <li>Sunrise <span>{{ activeDay.sunrise | utf | time }}</span></li>
        <li>Sunset <span>{{ activeDay.sunset | utf | time }}</span></li>
        <li>Description <span>{{ activeDay.weather[0].description }}</span></li>
    </ul>
</div>
</template>
w
<script>
import moment from 'moment'

export default {
    props: {
        day: {
            type: Object,
            required: true
        },
    },

    watch: {
        day(newVal) {
            this.activeDay = newVal
        }
    },

    data() {
        return {
            activeDay: []
        }
    },

    filters: {
        date(val) {
            return moment.unix(val).format('D.M')
        },

        time(val) {
            return val.slice(0, -3)
        },
    }

}
</script>

<style scoped>

    .weather-info {
        background-color: rgba(0,0,0,.2);
        color: rgb(187, 185, 185);
        margin-left: 5rem;
        padding: 1rem;
        text-align: left;
    }

    .weather-info li {
        display: flex;
        justify-content: space-between;
        padding: .5rem;
        border-bottom: 1px solid rgb(187, 185, 185);
    }

    .weather-info li:last-child {
        border-bottom: none
    }
@media only screen and (max-width: 830px) {
    .weather-info {
        margin-left: 1rem
    }
}

@media only screen and (max-width: 680px) {
    .weather-info {
        margin-left: 0;
    }
}

</style>    