<template>
    <div>
        <router-link :to="{ name: 'interactive' }" class="interactive">Go to interactive map</router-link>

        <ul class="slovakia">
            <li>
                <SVKMap />
            </li>
            <li v-for="(c, i) in city_list" :key="i"
            :style="{ position: 'absolute', top: c.position.p.top, left: c.position.p.left}">
                <router-link :to="{ name: 'home', params: { city: c.city.name }}" class="flex justify-center">
                    <span @mouseover="over = i" @mouseleave="over = null" style="z-index: 100000">
                        <SvgIcons :width="icon.width" :height="icon.height" :description="c.city.weather[0].description" />
                    </span>
                </router-link>
               
                <div class="weather-desc">
                    <p>{{ c.position.name }}</p>
                    <p style="font-weight: bold; font-size: .8rem">{{ c.city.main.temp | temp }}</p>
                </div>

                <CityPreviewModal :data="c" :hovered="over == i ? true : false" />
            </li> 
        </ul>
        <div class="mt-4">
            <h3 class="text-4xl pb-4 svk">Slovakia</h3>
            <ul class="weather-info">
                <li>Temperature <span>{{ svk.main.temp | temp }}</span></li>
                <li>Feels like <span>{{ svk.main.feels_like | temp }}</span></li>
                <li>Pressure <span>{{ svk.main.pressure }} hPa</span></li>
                <li>Humidity <span>{{ svk.main.humidity }} %</span></li>
                <li>Wind speed <span>{{ svk.wind.speed | kmH }}</span></li>
                <li>Description <span>{{ svk.weather[0].description }}</span></li>
            </ul>
        </div>
    </div>
</template>

<script>
import SVKMap from '@/components/svgs/SVKMap'
import SvgIcons from '@/components/svgs/SvgIcons'
import CityPreviewModal from '@/components/CityPreviewModal'
import { paths } from '@/assets/js/counties.js'
import MediaQueriesMixin from '@/mixins/MediaQueriesMixin.js'
import FilterCitiesMixin from '@/mixins/FilterCitiesMixin.js'

export default {
    mixins: [ MediaQueriesMixin, FilterCitiesMixin ],
    components: { CityPreviewModal, SVKMap, SvgIcons },
    
    props: {
        cities: {
            type: Array,
            required: true
        },
        svk: {
            type: Object,
            required: true
        }
    },

    data() {
        return {
            icon: {
                width: null,
                height: null
            },
            counties: paths,
            over: null,
            showModal: false,
            list: [
                 {
                    name: 'Poprad',
                    id: 723846,
                    p: {
                        left: '59%',
                        top: '18%'
                    }
                },
                {
                    name: 'Nitra',
                    id: 3058531,
                    p: {
                        top: '57%',
                        left: '18%'
                    }
                },
                 {
                    name: 'Bratislava',
                    id: 3060972,
                    p: {
                        top: '65%',
                        left: '0%'
                    }
                },
                {
                    name: 'Zilina',
                    id: 3056506,
                    p: {
                        left: '31%',
                        top: '10%'
                    }
                },
                {
                    name: 'Banska Bystrica',
                    id: 3343954,
                    p: {
                        top: '40%',
                        left: '35%'
                    }
                },
                {
                    name: 'Presov',
                    id: 865085,
                    p: {
                        left: '74%',
                        top: '25%'
                    }
                },
                {
                    name: 'Kosice',
                    id: 865084,
                    p: {
                        left: '68%',
                        top: '39%'
                    }
                },
                {
                    name: 'Senica',
                    id: 3057691,
                    p: {
                        left: '6%',
                        top: '37%'
                    }
                },
                {
                    name: 'Trencin',
                    id: 3343957,
                    p: {
                        left: '17%',
                        top: '27%'
                    }
                },
                {
                    name: 'Lucenec',
                    id: 3058986,
                    p: {
                        top: '54%',
                        left: '46%'
                    }
                },
                {
                    name: 'Humenne',
                    id: 724627,
                    p: {
                        top: '34%',
                        left: '85%'
                    }
                }
            ]
        }
    },

      watch: {
        width(size) {
           if(size <= 1050 && size > 830) {
              this.icon.width = 70
              this.icon.height = 70
           } else if (size <= 830 && size > 680) {
              this.icon.width = 60
              this.icon.height = 60
           } else if (size <= 680 && size > 650) {
             this.icon.width = 60
              this.icon.height = 60
           } else if (size <= 650 && size > 570 ) {
              this.icon.width = 50
              this.icon.height = 50
            } else if (size <= 570 && size > 530 ) {
            this.icon.width = 40
              this.icon.height = 40
            } else if (size <= 530 ) {
                this.icon.width = 30
              this.icon.height = 30    
           } else if (size > 1050) {
              this.icon.width = 70
              this.icon.height = 70
           }
      }
      }
}
</script>

<style>
    p {
        color: black;
        font-weight: bold;
    }

    .active {
        background-color: green
    }

    .selected {
        color: rgb(36, 255, 10);
    }
    .slovakia {
        margin-top: 2rem;
        position: relative;
        margin-top: 2rem
    }
    .interactive {
        color: rgb(192, 192, 192);
        position: relative;
        margin-top: 2rem
    }

    .interactive:hover {
        color: rgb(255,255,255);
        text-decoration: none;
    }
    
    .world-city {
        display: inline-block;
        width: 300px;
        height: 350px;
        background: red;
        margin: .5rem .2rem
    }
    .weather-info {
        background-color: rgba(0,0,0,.2);
        color: rgb(187, 185, 185);
        padding: 1rem;
        text-align: left;
    }

    .weather-info li {
        display: flex;
        justify-content: space-between;
        padding: .5rem;
        border-bottom: 1px solid rgb(187, 185, 185);
    }

    .weather-info li:last-child {
        border-bottom: none
    }


    @media only screen and (max-width: 800px) {
        .weather-desc {
            display: none
        }
    }
    @media only screen and (max-width: 610px) {
        .svk {
            font-size: 2rem;
        }
    }
    @media only screen and (max-width: 380px) {
        .svk {
            font-size: 1.5rem;
        }
    }
</style>