<template>
  <div class="flex text-center container">
      <ul class="city-list w-full block" v-if="cities.length !== 0">
          <router-link class="city-card" tag="li" :to="{ name: 'home', params: { city: city.name}}" v-for="(city, i) in cities" :key="i">
              <div class="card-container">
                <div class="flex justify-center city-desc">
                    <SvgIcons :description="city.weather[0].description" :width="icon.width" :height="icon.height" />
                </div>
                    <p class=" pb-2 city-name">{{ city.name }}</p>
                <div>
                    <div>{{ city.main.temp | temp }}</div>
                    <div>{{ city.weather[0].description }}</div>
                </div>
              </div>
          </router-link>
      </ul>
  </div>
</template>

<script>
import SvgIcons from '@/components/svgs/SvgIcons'
import MediaQueriesMixin from '@/mixins/MediaQueriesMixin.js'

export default {
    mixins: [ MediaQueriesMixin ],
    components: { SvgIcons },
    props: {
        activeID: {
            type: String,
        }
    },

    watch: {
        /** ID kraja, po zmene sa posle api req na mesta kraja*/
        activeID() {
            this.fetchCities()
        },

        width(size) {
            if(size <= 420 && size > 320 ) {
                this.icon.width = 30
                this.icon.height = 30
            }
        }
    },

    methods: {
        /** api req na mesta kraja */
        fetchCities() {
            this.$store.dispatch('weather/getCountyCities', this.activeCities).then(res => {
                this.cities = res
            })
        },

        /** ziskanie vsetkych IDciek miest do jedneho pola */
        getIDs() {
            for(let i = 0; i <= this.countiesCities.length - 1; i++) {
                this.allIDs.push(this.countiesCities[i].cities)
            }
            this.arr = [].concat(...this.allIDs)

        },
    },

    mounted() {
        this.getIDs()
        this.$store.dispatch('weather/getCountyCities', this.countiesCities[0].cities).then(res => {
            this.cities = res
       }).catch(() => {
            alert('err')
        })
    },

    data() {
        return {
            icon: {
                width: 40,
                height: 40
            },
            arr: [],
            allIDs: [],
            cities: [],
             countiesCities: [
                { 
                    id: 'SK-BL',
                    cities: [3058897, 3058210, 3060972, 3057692] },
                {
                    id: 'SK-PV',
                    cities: [723846, 724503, 723505, 724294, 723674, 865085, 725168, 723417, 723455, 724156, 723195,
                724627, 723559] },
                { 
                    id: 'SK-KI',
                    cities: [723713, 723526, 724800, 865084, 724144, 723358, 723554] },
                { 
                    id: 'SK-ZI',
                    cities: [3060852, 3060835, 3059179, 3056506, 3058780, 3057086, 3057789, 3060405, 3058579, 3057063, 3059050] },
                { 
                    id: 'SK-BC',
                    cities: [3056589, 3056089, 3056523, 3061184, 3059244, 3056459, 3343954, 3060589, 3058986, 3058083, 
                3060950, 723736, 723747] },
                { 
                    id: 'SK-TC',
                    cities: [3058611, 3058477, 3343957, 3061188, 3058268, 3058000,
                3059796, 3057963, 3058060] },
                { 
                    id: 'SK-NI',
                    cities: [3059436, 3058472, 3057769, 3058531, 3057174, 3056495, 3059101]},
                { 
                    id: 'SK-TA',
                    cities: [3057691, 3057630, 3058202, 3057124, 3060095, 3060219, 3060308]},
             ]
        }
    },

    computed: {
        activeCities() {
            for(let i = 0; i <= this.countiesCities.length; i++) {
                if(this.countiesCities[i].id == this.activeID) {
                    return this.countiesCities[i].cities
                }
            }
            return false
        }
    },

}
</script>

<style scoped>
    .container {
        margin-top: 1rem;
    }
    .city-list {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin-top: 2rem;
        text-align: center
    }
    
    .city-card:hover {
        transform: scale(1.1);
        background-color: rgba(0,0,0,.2);
        cursor: pointer;
        box-shadow: 0 2px 2px 2px rgba(0,0,0, .7)
    }
    .card-container {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

    }
    .city-card {
        transition: all .1s ease-in-out;
        padding: 1rem;
        margin-bottom: 1rem;
        width: 10rem;
        border-radius: 7px;
    }

    .city-desc {
        display: flex;
        height: 100%;
        align-items: space-between;
    }

    .container {
        position: relative
    }

    @media screen and (max-width: 780px ) {
        .city-card {
            width: 7rem;
            padding: .5rem
        }
    }

    @media screen and (max-width: 780px ) {
        .city-card {
            font-size: .8rem;
        }
    }
    @media screen and (max-width: 550px ) {
        .container {
            top: -5rem
        }
    }
    @media screen and (max-width: 420px ) {
        .container {
            top: -11.5rem
        }
    }
</style>