import Vue from 'vue'
import App from './App.vue'
import store from './store/store'
import router from './router'

import Celsius from '@/filters/FormatTemp'
import KmH from '@/filters/FormatWind'
import UTF from '@/filters/UnixtoUTF'

Vue.config.productionTip = false

Vue.filter('temp', Celsius)
Vue.filter('KmH', KmH)
Vue.filter('utf', UTF)


new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
