export default {

    data() {
        return {
            city_list: []
        }
    },
    
    methods: {
        filterCities() {
            for(let i = 0; i < this.cities.length; i++ ) {
                for(let a = 0; a < this.list.length; a++) {
                    if(this.list[a].id == this.cities[i].id) {
                        this.city_list.push({ city: this.cities[i], position: this.list[a]})
                    }
                }
            }
        }
    },

    mounted() {
        this.filterCities()
    }
}