<template>
  <div 
    class="forecast-card" 
    @click="$emit('activateDay', { day, id }), hovered = false"
    @mouseover="mouseover"
    @mouseleave="mouseleave"
    :class="{ active: isActive, hover: hovered }"
    >
    <SvgIcons :description="day.weather[0].description" :height="icon.height" :width="icon.width" />
    
    <div>
        <p class="time">{{ day.dt | daytime  }}</p>
        <p class="time">{{ day.dt | date }}</p>
    </div>
    <div>
        <p class="temp">{{ day.temp.day | temp }} / {{ day.temp.night | temp }}</p>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
import SvgIcons from '@/components/svgs/SvgIcons'

export default {
    components: { SvgIcons },
    props: {
        day: {
            type: Object,
            required: true
        },
        id: {
            type: Number,
            required: true
        },
        isActive: {
            type: Boolean,
            required: true
        },
    },

    data() {
        return {
            cas: this.day.dt,
            hovered: false,
            icon: {
                width: 50,
                height: 50
            }
        }
    },
   
    filters: {
        daytime(val) {
            return moment.unix(val).format('ddd')
        },

        date(val) {
            return moment.unix(val).format('D.M')
        }
    },

    methods: {
        mouseover() {
            if(!this.hovered && !this.isActive ) 
                this.hovered = true
        },
        mouseleave() {
            this.hovered = false
        }
    }
}
</script>
   
<style scoped>
   
    .forecast-card {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: .5rem 1rem;
        color: #cbe5ff;
        transition: all .1s ease-in
    }

    .temp, .time {
        color: #cbe5ff;
        font-weight: 100
    }

    .time {
        margin: 0 1rem
    }
    .hover {
        background-color: rgba(0, 0, 0, .2);
        cursor: pointer;
    }

    .active {
        background-color:rgba(0,0,0,0.4);
    }

    #day {
        margin: 0 1rem
    }
    p {
        margin: 0
    }

    @media only screen and (max-width: 830px) {
        .forecast-card {
            font-size: .8rem;
        }
    }
    
    @media only screen and (max-width: 680px) {
        .forecast-card {
            display: inline-block;
            text-align: center
        }
    }
</style>