<template>
    <div style="chartContainer">
        <canvas ref="myChart" class="chart"></canvas>
    </div>
</template>

<script>
import Chart from 'chart.js'

import MediaQueriesMixin from '@/mixins/MediaQueriesMixin.js'

export default {
    mixins: [ MediaQueriesMixin ],

    props: {
        forecast: {
            type: Array,
            required: true
        }
    },

    data() {
        return {
            yAxes: {
                showTicks: {
                    temp: true,
                    rain: true
                }
            },
            xAxes: {
                showTicks: {
                    time: true
                }
            }
        }
    },

    mounted() {
        let forecastArr = this.forecast.slice(0, 24)
        let times = forecastArr.map(time => {
            let date = new Date(time.dt * 1000)
            let hours = date.getHours()
            let minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
            return hours + ':' + minutes
        })

        let temp = forecastArr.map(temp => {
            return temp.temp
        })

        let rain = forecastArr.map(rain => {
            if(rain.rain) {
                return rain.rain["1h"]
            } else {
                return 0
            }
        })



        let ctx = this.$refs.myChart
        new Chart(ctx, {
        type: 'line',
        data: {
        labels: times,
        datasets: [{
            label: this.width <= 470 ? 'Temp (C)' : 'Temperature (C)',
            yAxisID: 'temp',
            data: temp,
            borderColor: 'orange',
            borderWidth: 2
        },
        {   
            label: this.width > 470 ? 'Precipitation (mm)' : 'Precip (mm)',
            yAxisID: 'rain',
            data: rain,
            backgroundColor:'blue',
            borderColor: 'darkblue',
            borderWidth: 1,
            type: 'bar'
        }]
        },
        options: {
            
            maintainAspectRatio: true,
            scales: {
                yAxes: [{
                    id: 'temp',
                    position: 'left',
                    ticks: {
                        display: this.width <= 470 ? false : true,
                        beginAtZero: true
                    }
                },
                {
                    display: this.width > 470 ? true : false,
                    id: 'rain',
                    position: 'right'
                }
                ],
                xAxes: [{
                    ticks: {
                        display: this.width > 470 ? true : false
                    }
                }]
            }
        }
        });
    }
}
</script>

<style scoped>
.chartContainer {
    position: relative;
}
.chart {
    width: 500px
}
    @media screen and (max-width: 880px) {
        .chart {
            width: 400px
        }
    }

    @media screen and (max-width: 600px) {
        .chart {
            width: 400px
        }
    }

    @media screen and (max-width: 470px) {
        .chart {
            width: 300px
        }
    }

    @media screen and (max-width: 360px) {
        .chart {
            width: 245px
        }
    }
</style>