import axios from 'axios'
const api_key = '4605f1873def8d13caa21d6670e9e464'

const apiClient = axios.create({
    baseURL: 'https://api.openweathermap.org',
    withCredentials: false,
    headers: {
        Accept: 'aplication/json',
        'Content-Type': 'aplication/json'
    }
})

export default {
    getByCityName(name) {
        return apiClient.get('/data/2.5/weather?q=' + name + '&appid=' + api_key+'&units=metric')
    },
    
    getMultipleCities(cityIDs) {
        return apiClient.get('/data/2.5/group?id=' + cityIDs + '&appid='+ api_key +'&units=metric')
    }, 


    getOneCall(lon, lat) {
        return apiClient('/data/2.5/onecall?lat='+ lat +'&lon='+ lon +'&exclude={part}&appid=' + api_key +'&units=metric')
    },

    getByCoords(lon, lat) {
        return apiClient('/data/2.5/weather?lat=' + lat + '&lon='+ lon +'&appid=' + api_key +'&units=metric' )
    },

    fetchSlovakia() {
        return apiClient('/data/2.5/weather?q=Slovakia&appid='+ api_key +'&units=metric')
    }
}