<template>
<div class="flex flex-column py-6 ">
  <nav class="app-menu w-full flex justify-between">
        <ul class="main-nav">
            <li class="nav-item flex items-center">
            <router-link :class="{selected: $route.name == 'my-location'}" :to="{ name: 'my-location'}">My location</router-link> 
            </li>
            <li class="nav-item flex items-center">
            <router-link :class="{selected: $route.name == 'slovakia' || $route.name == 'interactive' } " :to="{ name: 'slovakia' }">Slovakia</router-link> 
            </li>
            <li class="nav-item flex items-center">
            <router-link :class="{selected: $route.name == 'europe' }" :to="{ name: 'europe' }">Europe</router-link> 
            </li>
        </ul>

        <div style="display: flex ; align-self: middle">
            <p class="flex font-normal date items-center"><span>{{ date }}</span><span class="ml-3 mr-4">{{ time }}</span></p>
            <form @submit.prevent="sendReq" class="flex self-middle">
                <input type="text" v-model="city" placeholder="Search city" class="self-center py-1 px-3 city-input">
            </form>
        </div>
  </nav>
    <nav class="mobile-menu">
        <div class="flex justify-between">
            <a href="#" @click.prevent='openMenu = !openMenu' class="self-center">
                <svg height="30px" fill="#bfe5ff" viewBox="0 -53 384 384" width="30px" xmlns="http://www.w3.org/2000/svg">
                <path d="m368 154.667969h-352c-8.832031 0-16-7.167969-16-16s7.167969-16 16-16h352c8.832031 0 16 7.167969 16 16s-7.167969 16-16 16zm0 0"/>
                <path d="m368 32h-352c-8.832031 0-16-7.167969-16-16s7.167969-16 16-16h352c8.832031 0 16 7.167969 16 16s-7.167969 16-16 16zm0 0"/>
                <path d="m368 277.332031h-352c-8.832031 0-16-7.167969-16-16s7.167969-16 16-16h352c8.832031 0 16 7.167969 16 16s-7.167969 16-16 16zm0 0"/></svg>
            </a>
            
            <div class="flex self-middle">
                <form @submit.prevent="sendReq" class="flex; self-middle">
                    <input type="text" v-model="city" placeholder="Search city" class="self-center py-1 px-3 city-input">
                </form>
            </div>
        </div>
        <transition name="slide">
            <ul v-if="openMenu" class="menu-collapse">
                <li class="nav-item"
                    @mouseenter="mouseover('my-location')"
                    @mouseleave="mouseleave"
                >
                    <router-link @click="openMenu = false" :class="{navselected: $route.name == 'my-location', hovered: hoveredLink == 'my-location' }" class="nav-link" 
                    :to="{ name: 'my-location'}"
                    >My location</router-link> 
                </li>
                <li class="nav-item" 
                    @mouseenter="mouseover('slovakia')"
                    @mouseleave="mouseleave">
                    <router-link @click="openMenu = false" :class="{navselected: $route.name == 'slovakia', hovered: hoveredLink == 'slovakia' }" class="nav-link" 
                        :to="{ name: 'slovakia' }"
                        >Slovakia</router-link> 
                </li>
                <li class="nav-item" 
                    @mouseenter="mouseover('europe')"
                    @mouseleave="mouseleave"
                >
                    <router-link @click="openMenu = false" :class="{navselected: $route.name == 'europe', hovered: hoveredLink == 'europe' }" class="nav-link" 
                        :to="{ name: 'europe' }"
                        >Europe</router-link> 
                </li>
            </ul>
        </transition>
        </nav>
    </div>
</template>

<script>
import CurrentTimeMixin from '@/mixins/CurrentTimeMixin.js'

export default {
    mixins: [ CurrentTimeMixin ],

    data() {
        return {
            city: '',
            openMenu: false,
            activeLink: null,
            hoveredLink: null
        }
    },

    mounted() {
        this.activeLink = this.$route.name
    },

    methods: {
        sendReq() {
            this.$store.commit('weather/SET_CITY', this.city )
            this.$router.push({ name: 'home', params: { city: this.city }})
            this.city = ''
        },

        mouseover(link) {
            this.hoveredLink = link
        },

        mouseleave() {
            this.hoveredLink = null
        }
    }
}
</script>

<style scoped>

    .menu-collapse {
        overflow: hidden;
        margin-top: 1rem;
    }

    .menu-collapse li {
        width: 100%;
        padding: 0
    }

    .menu-collapse a {
        display: block
    }

    .nav-link {
        padding: .7rem.5rem;
        background-color: rgba(0,0,0,.5);
        border-bottom: 1px solid rgb(0,0,0)
    }

    .hovered {
        background-color: rgba(0,0,0,.2)
    }

    .navselected {
        background: rgba(0,0,0,.8)
    }


    .main-nav, .app-menu {
        display: flex;
    }
    .nav-item {
        padding: .3rem;
    }
    a {
        color: rgb(192, 192, 192);
        text-decoration: none;
    }
    
    .selected {
        color: rgb(218, 216, 216);
        font-weight: bold;
    }

    .date {
    color: #cbe5ff
    }

    .city-input {
        border-radius: 1rem;
        outline: none;
        background-color: #c1c1c1;
        color: #000;
        border: 1px solid #636384
    }

    .mobile-menu {
        display: none;
        width: 100%
    }

    .mobile-menu ul {
        display: block
    }


    .slide-enter-active, 
    .slide-leave-active {
        transition: all .2s ease-in-out
    }

    .slide-enter, .slide-leave-to {
        height: 0;
    }

    .slide-enter-to, .slide-leave {
        height: 8.883125rem;
} 
    @media only screen and (max-width: 650px) {

        .app-menu {
            display: none
        }
        
        .mobile-menu {
            display: flex;
            flex-direction: column;
        }
    }
</style>    