<template>
  <div>
    <ul class="europe-map">
      <li>
        <EuropeMap />
      </li>
       <li v-for="(c, i) in city_list" :key="i"
            :style="{ position: 'absolute', top: c.position.p.top, left: c.position.p.left}">
                <router-link :to="{ name: 'home', params: { city: c.city.name }}">
               
                    <span @mouseover="over = i" @mouseleave="over = null" style="z-index: 100000">
                        <SvgIcons :width="icon.width" :height="icon.height" :description="c.city.weather[0].description" />
                    </span>
                </router-link>
               
                <div class="city-label">
                    <p>{{ c.position.name }}</p>
                    <p style="font-weight: bold; font-size: .8rem">{{ c.city.main.temp | temp }}</p>
              </div>
              <CityPreviewModal :data="c" :hovered="over == i ? true : false" />
          </li> 
    </ul>
  </div>
</template>

<script>
import MediaQueriesMixin from '@/mixins/MediaQueriesMixin.js'
import FilterCitiesMixin from '@/mixins/FilterCitiesMixin.js'

import EuropeMap from '@/components/svgs/EuropeMap'
import CityPreviewModal from '@/components/CityPreviewModal'
import SvgIcons from '@/components/svgs/SvgIcons'


export default {
  mixins: [ MediaQueriesMixin, FilterCitiesMixin ],
  components: { EuropeMap,CityPreviewModal, SvgIcons },

  props: {
    cities: {
      type: Array,
      required: true 
    }
  },

  data() {
  return {
    icon: {
      width: null,
      height: null
    },
    list: [
            {
              name: 'Paris',
              id: 2988507,
              p: {
                  top: '38%',
                  left: '35%'
              }
            },
            {
              name: 'Dublin',
              id: 2964574,
              p: {
                  top: '22%',
                  left: '15%'
              }
            },
             {
              name: 'Zurich',
              id: 2657896,
              p: {
                  top: '45%',
                  left: '50%'
              }
            },
             {
              name: 'Kyiv',
              id: 703448,
              p: {
                  top: '32%',
                  left: '94%'
              }
            },
              {
              name: 'Tunis',
              id: 2464470,
              p: {
                  top: '84%',
                  left: '53%'
              }
            },
             {
              name: 'Athens',
              id: 264371,
              p: {
                  top: '80%',
                  left: '86%'
              }
            },
            {
              name: 'Bucharest',
              id: 683506,
              p: {
                  top: '55%',
                  left: '90%'
              }
            },
             {
              name: 'Vienna',
              id: 2761369,
              p: {
                  top: '42%',
                  left: '69%'
              }
            },
             {
              name: 'Rome',
              id: 3169070,
              p: {
                  top: '65%',
                  left: '58%'
              }
            },
             {
              name: 'London',
              id: 2643743,
              p: {
                  top: '28%',
                  left: '31%'
              }
            },
             {
              name: 'Copenhagen',
              id: 2618425,
              p: {
                  top: '12%',
                  left: '57%'
              }
            },
             {
              name: 'Budapest',
              id: 3054643,
              p: {
                  top: '44%',
                  left: '74%'
              }
            },
            {
              name: 'Prague',
              id: 3067696,
              p: {
                  top: '33%',
                  left: '65%'
              }
            },
            {
              name: 'Berlin',
              id: 2950159,
              p: {
                left: '61%',
                top: '26%'
              }
            },
            {
               name: 'Warsaw',
               id: 756135,
               p: {
                 left: '80%',
                 top: '25%'
               }
            },
            {
              name: 'Madrid',
              id: 3117735,
              p: {
                left: '21%',
                top: '71%'
              }
            }
            ],
            over: null,
            showModal: false,

      }
  },

      watch: {
        width(size) {
           if(size <= 1050 && size > 830) {
              this.icon.width = 60
              this.icon.height = 60
           } else if (size <= 830 && size > 700 ) {
              this.icon.width = 50
              this.icon.height = 50
            } else if (size <= 700 && size > 400 ) {
                this.icon.width = 30
              this.icon.height = 30    
           } else if (size <= 400 ) {
             this.icon.width = 20
             this.icon.height = 20
           } else if (size > 1050 ) {
             this.icon.width = 50
             this.icon.height = 50
        }
      }}
}
</script>

<style>
  .europe-map {
    text-align: -webkit-center;
    position: relative;
    top: -1rem
  }

  @media screen and (max-width: 850px ) {
    .city-label {
      display: none
    }
  }
</style>